import {
  DeleteOutlined,
  LoadingOutlined,
  EyeOutlined,
  EditOutlined,
  PlusCircleOutlined
} from '@ant-design/icons'
import { Button, Modal, Space, Spin, Table, Switch } from 'antd'
import Avatar from 'antd/lib/avatar/avatar'
import { UpdateCourse } from 'pages/CreateCourse/redux/actions'
import React, { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { COLOR } from 'ultis/functions'
import '../dashboard.css'
import { DeleteCourse, GetAllCourses } from '../redux/actions'
import { getColumnSearchProps } from './searchInput'

const loadingIcon = (
  <LoadingOutlined style={{ fontSize: 30, color: COLOR.primary1 }} spin />
)

function CoursesList() {
  const courseList = useSelector(state => state.Dashboard.courseList)
  const isLoading = useSelector(state => state.Dashboard.isLoading)
  const dispatch = useDispatch()
  const [searchText, setSearchText] = useState('')
  const [searchedColumn, setSearchColumn] = useState('')
  const refInput = useRef()
  const history = useHistory()

  useEffect(() => {
    dispatch(GetAllCourses.get())
  }, [])

  const handleDelete = record => {
    Modal.confirm({
      title: 'Confirm',
      icon: <DeleteOutlined style={{ color: COLOR.primary1 }} />,
      content: `Do you confirm to delete ${record.name}?`,
      okText: 'Confirm',
      cancelText: 'Cancel',
      centered: true,
      okButtonProps: { style: { backgroundColor: COLOR.primary1 } },
      onOk: () => {
        dispatch(DeleteCourse.get(record.id))
        Modal.destroyAll()
      }
    })
  }

  const handleView = record => {
    history.push(`/course/${record.id}`)
  }

  const handleEdit = record => {
    history.push(`/course/${record.id}/edit`)
  }

  const courseColumns = [
    {
      ...getColumnSearchProps(
        'name',
        'Enter name to find',
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
      title: 'Course',
      dataIndex: 'name',
      key: 'name',
      sorter: (a, b) => a.name.localeCompare(b.name),
      render: (value, record) => {
        return (
          <Space>
            <Avatar shape="square" size={56} src={record?.thumbnail} />
            <span>{value}</span>
          </Space>
        )
      }
    },
    {
      ...getColumnSearchProps(
        'category',
        'Enter category to find',
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
      title: 'Category',
      dataIndex: 'category',
      key: 'category',
      sorter: (a, b) => a.category.localeCompare(b.category)
    },
    {
      title: 'Enrollment',
      dataIndex: 'enrollCount',
      key: 'enrollCount',
      sorter: (a, b) => Number(a.enrollCount) > Number(b.enrollCount)
    },
    {
      title: 'Rating',
      dataIndex: 'rating',
      key: 'rating',
      sorter: (a, b) => a.rating > b.rating
    },
    {
      title: 'Price',
      dataIndex: 'price',
      key: 'price',
      sorter: (a, b) => a.price > b.price
    },
    {
      ...getColumnSearchProps(
        'teacherName',
        'Enter teacher name to find',
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
      title: 'Teacher',
      dataIndex: 'teacherName',
      key: 'teacherName',
      sorter: (a, b) => a.teacherName.localeCompare(b.teacherName),
      render: (value, record) => {
        return (
          <Space>
            <Avatar size={56} src={record?.teacherAvatar} />
            <span>{value}</span>
          </Space>
        )
      }
    },
    {
      title: 'Public',
      dataIndex: 'publicStatus',
      key: 'publicStatus',
      render: (value, record) => {
        return (
          <Space>
            <Switch
              defaultChecked={value}
              onChange={checked =>
                dispatch(
                  UpdateCourse.get({
                    id: record.id,
                    data: {
                      publicStatus: checked
                    }
                  })
                )
              }
            />
          </Space>
        )
      }
    },
    {
      title: 'Action',
      key: 'action',
      render: (value, record) => {
        return (
          <Space>
            <EyeOutlined
              style={{ fontSize: 20 }}
              onClick={() => handleView(record)}
            />
            <EditOutlined
              style={{ fontSize: 20, color: COLOR.primary1 }}
              onClick={() => handleEdit(record)}
            />
            <DeleteOutlined
              style={{ fontSize: 20, color: '#FF0000' }}
              onClick={() => handleDelete(record)}
            />
          </Space>
        )
      }
    }
  ]

  if (isLoading) {
    return (
      <div className="chooseContainer">
        <Spin indicator={loadingIcon} />
      </div>
    )
  }

  return (
    <>
      <div className="chooseContainer">
        <span className="titleTopic">Courses</span>
        <Button
          type="primary"
          icon={<PlusCircleOutlined />}
          style={{ width: 200, marginBottom: 32 }}
          onClick={() => {
            history.push('/create')
          }}
          size="large"
        >
          Add new course
        </Button>
        <Table columns={courseColumns} dataSource={courseList} />
      </div>
    </>
  )
}

export default CoursesList
